import { useEffect, useState } from "react";
import { Navigate, useLocation } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { message } from "antd";
import { logout } from "../../store/actions/authActions";
import Loading from "../Common/Loading";

/**
 * ProtectedRoute - Component  bo v routes yu cu authentication
 * T ng kim tra session v redirect v login nu ht hn
 */
const ProtectedRoute = ({ children }) => {
  const location = useLocation();
  const dispatch = useDispatch();
  const { isAuthenticated, sessionId } = useSelector((state) => state.auth);
  const [isChecking, setIsChecking] = useState(true);
  const [isValid, setIsValid] = useState(false);

  useEffect(() => {
    const checkSession = async () => {
      // Nu khng c authentication state, kim tra token trong localStorage
      const token =
        localStorage.getItem("authToken") || localStorage.getItem("token");
      const storedSessionId = localStorage.getItem("sessionId");

      if (!token && !isAuthenticated) {
        // Khng c token v khng authenticated
        setIsChecking(false);
        setIsValid(false);
        return;
      }

      // Nu c token nhng cha c trong Redux state, th validate
      if (token && !isAuthenticated) {
        try {
          // Kim tra session vi backend
          const API_BASE_URL =
            import.meta.env.VITE_API_URL ||
            import.meta.env.REACT_APP_API_URL ||
            "http://localhost:3001";

          const token =
            localStorage.getItem("authToken") || localStorage.getItem("token");

          const response = await fetch(`${API_BASE_URL}/api/auth/verify`, {
            method: "GET",
            headers: {
              "Content-Type": "application/json",
              ...(token && { Authorization: `Bearer ${token}` }),
            },
          });

          if (response.ok) {
            const data = await response.json();
            if (data.valid && data.success) {
              // Session hp l
              setIsValid(true);
            } else {
              // Session khng hp l
              setIsValid(false);
              // Clear invalid tokens
              localStorage.removeItem("authToken");
              localStorage.removeItem("token");
              localStorage.removeItem("sessionId");
            }
          } else {
            // Session expired hoc invalid (401)
            setIsValid(false);
            localStorage.removeItem("authToken");
            localStorage.removeItem("token");
            localStorage.removeItem("sessionId");
          }
        } catch (error) {
          console.error("Session check error:", error);
          setIsValid(false);
          localStorage.removeItem("authToken");
          localStorage.removeItem("token");
          localStorage.removeItem("sessionId");
        }
      } else if (isAuthenticated) {
        //  authenticated trong Redux, kim tra session vi backend
        if (sessionId || storedSessionId) {
          try {
            const API_BASE_URL =
              import.meta.env.VITE_API_URL ||
              import.meta.env.REACT_APP_API_URL ||
              "http://localhost:3001";

            const token =
              localStorage.getItem("authToken") ||
              localStorage.getItem("token");

            const response = await fetch(`${API_BASE_URL}/api/auth/verify`, {
              method: "GET",
              headers: {
                "Content-Type": "application/json",
                ...(token && { Authorization: `Bearer ${token}` }),
              },
            });

            if (response.ok) {
              const data = await response.json();
              if (data.valid && data.success) {
                setIsValid(true);
              } else {
                // Session khng hp l
                setIsValid(false);
                // Auto logout
                try {
                  await dispatch(logout(false));
                } catch (e) {
                  // Ignore logout errors
                }
                message.warning(
                  "Phin ng nhp  ht hn. Vui lng ng nhp li.",
                );
              }
            } else if (response.status === 401) {
              // Session expired
              setIsValid(false);
              // Auto logout
              try {
                await dispatch(logout(false));
              } catch (e) {
                // Ignore logout errors
              }
              message.warning(
                "Phin ng nhp  ht hn. Vui lng ng nhp li.",
              );
            } else {
              // Other error
              setIsValid(false);
              try {
                await dispatch(logout(false));
              } catch (e) {
                // Ignore logout errors
              }
            }
          } catch (error) {
            console.error("Session verification error:", error);
            // Nu li network, cho php truy cp (c th l backend cha chy)
            // Nhng nu l 401, th session  ht hn
            if (
              error.message?.includes("401") ||
              error.message?.includes("Unauthorized")
            ) {
              setIsValid(false);
              try {
                await dispatch(logout(false));
              } catch (e) {
                // Ignore logout errors
              }
              message.warning(
                "Phin ng nhp  ht hn. Vui lng ng nhp li.",
              );
            } else {
              // Network error, cho php truy cp (c th backend cha chy)
              setIsValid(true);
            }
          }
        } else {
          // C authentication nhng khng c sessionId
          setIsValid(true); // Cho php truy cp
        }
      } else {
        setIsValid(false);
      }

      setIsChecking(false);
    };

    checkSession();

    // Kim tra session nh k mi 5 pht
    const interval = setInterval(
      () => {
        checkSession();
      },
      5 * 60 * 1000,
    ); // 5 minutes

    return () => clearInterval(interval);
  }, [isAuthenticated, sessionId, dispatch]);

  // ang kim tra
  if (isChecking) {
    return <Loading text="ang kim tra phin ng nhp..." />;
  }

  // Khng hp l hoc khng authenticated, redirect v login
  if (!isValid || !isAuthenticated) {
    // Clear tokens trc khi redirect
    localStorage.removeItem("authToken");
    localStorage.removeItem("token");
    localStorage.removeItem("sessionId");

    // Redirect v login vi returnUrl  quay li sau khi login
    const returnUrl = location.pathname !== "/login" ? location.pathname : "/";
    return (
      <Navigate
        to={`/login?returnUrl=${encodeURIComponent(returnUrl)}`}
        replace
      />
    );
  }

  // Hp l v  authenticated, render children
  return children;
};

export default ProtectedRoute;
