// Preload critical components
const preloadComponent = (componentLoader) => {
  componentLoader();
};

// Preload dashboard on app start
if (typeof window !== "undefined") {
  setTimeout(() => {
    preloadComponent(() => import("./components/Dashboard/LiveDashboard"));
  }, 2000);
}

